//eslint-disable-next-line
<template>
<div>
  <h1>Расчет выгоды</h1>
  <div class="product-block">

    <my-input
      :info="infoPrice1"
      v-model.number='price1'
    />

    <my-input
      :info="infoQuantity1"
      v-model.number='quantity1'
    />
  </div>

  <div class="product-block">

    <my-input
      :info="infoPrice2"
      v-model.number='price2'
    />

    <my-input
      :info="infoQuantity2"
      v-model.number='quantity2'
    />
  </div>

  <button
    class='btn'
    @click='result'
  >
    Проверить
  </button>

  <my-dialog v-model:show="dialogVisible" >
    <p><strong>{{ discount.split('. ')[0] }}</strong></p>
    <p>{{ discount.split('. ')[1] }}</p>
    <p>{{ discount.split('. ')[2] }}</p>
  </my-dialog>

</div>
</template>

<script>

import MyInput from '@/components/MyInput'
import MyDialog from '@/components/MyDialog'
export default {
  name: 'App',
  components: {
    MyInput,
    MyDialog
  },
  data () {
    return {
      price1: '',
      infoPrice1: 'Стоимость первого товара',
      quantity1: '',
      infoQuantity1: 'Количество (объём, масса) первого товара',
      price2: '',
      infoPrice2: 'Стоимость второго товара',
      quantity2: '',
      infoQuantity2: 'Количество (объём, масса) второго товара',
      dialogVisible: false
    }
  },
  methods: {
    result () {
      if (!this.price1 || !this.quantity1 || !this.price2 || !this.quantity2) {
        this.clearing()
        alert('Некорректно заполнены поля')
        return
      }
      this.dialogVisible = true
    },
    clearing () {
      this.price1 = this.quantity1 = this.price2 = this.quantity2 = ''
    }
  },
  computed: {
    discount () {
      const cost1 = this.price1 / this.quantity1
      const cost2 = this.price2 / this.quantity2
      this.clearing()
      if (cost1 < cost2) {
        return `Выгоднее покупать товар №1. Одна единица данного товара стоит ${Math.ceil(cost1 * 100) / 100} ₽. Одна
        же единица товара №2 стоит ${Math.ceil(cost2 * 100) / 100} ₽.`
      } else if (cost1 > cost2) {
        return `Выгоднее покупать товар №2. Одна единица данного товара стоит ${Math.ceil(cost2 * 100) / 100} ₽. Одна
        же единица товара №1 стоит ${Math.ceil(cost1 * 100) / 100} ₽.`
      } else {
        return `Без разницы, какой товар брать. Одна единица каждого из товаров стоит ${Math.ceil(cost1 * 100) / 100} ₽. То есть стоимость обоих товаров одинакова.`
      }
    }
  }
}
</script>

