<template>
  <div class="input-block">
    <label
      class="label"
      for='info'
    >
      {{ info }}:
    </label>
    <input
      id='info'
      :value='modelValue'
      @input="updateInput"
      class="input"
      type="number"
    />
  </div>

</template>

<script>
export default {
  name: 'MyInput',
  props: {
    modelValue: [Number],
    info: [String]
  },
  methods: {
    updateInput (event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
}
</script>

<style scoped>
.input-block {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
}
.label {
  flex: 0 0 60%;
  font-size: 30px;
  color: black;
}
.input {
  flex: 0 0 30%;
  width: 95%;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 22px;
  border-radius: 10px;
  text-align: center;
}
.input:focus {
  outline: 2px solid blue;
  transform: scale(1.2);
}
@media (max-width: 850px) {
  .label {
    font-size: 24px;
  }
}
@media (max-width: 450px) {
  .label {
    font-size: 20px;
  }
}
@media (max-width: 350px) {
  .label {
    font-size: 18px;
  }
}
</style>
